html[dir="rtl"] body {
  text-align: right; }
  html[dir="rtl"] body * {
    letter-spacing: 0; }
  html[dir="rtl"] body .seeWrap .seeSvgBox {
    right: auto;
    left: -35%;
    transform: scaleX(1); }
  html[dir="rtl"] body .outSeeSvgBox {
    right: auto;
    left: 0;
    transform: scaleX(1); }
  html[dir="rtl"] body .outObjSvgBox {
    right: 0;
    left: auto;
    transform: scaleX(-1); }
